<template>
    <main>
        <div class="row">
            <div class="panel col wow fadeInUp" style="max-width: 500px; margin: auto">
                <div class="panel-header">
                    <h3>Dodawanie nowej roli</h3>
                </div>
                <form>
                    <label for="roleName">Nazwa roli</label>
                    <input type="text" id="roleName" v-model="roleName" autofocus>
                    <button v-if="!isEdit" @click="addRole" :disabled="roleName.length < 3">Dodaj</button>
                    <button v-else @click="saveRole" :disabled="roleName.length < 3">Zapisz</button>
                </form>
            </div>
        </div>
    </main>
</template>

<script>
    export default {
        name: "RoleAdd",
        props: ['id'],
        data(){
            return{
                roleName: '',
                isEdit: this.id !== 'add'
            }
        },
        methods:{
            validForm(){
                try {
                    let valid = new window.Validator({
                        "nazwa roli": this.roleName,
                    });
                    valid.get("nazwa roli").length(3,50);
                }catch (e) {
                    return false;
                }
                return true;
            },
            addRole(){
                if(!this.validForm()) return;

                axios.post(`/role`,{
                    name: this.roleName
                })
                    .then((response) => {
                        this.$router.push('/permission');
                    })
            },
            saveRole(){
                if(!this.validForm()) return;

                axios.put(`/role/${this.id}`,{
                    name: this.roleName
                })
                    .then((response) => {
                        this.$router.push('/permission');
                    })
            }
        },
        mounted(){

            if(this.isEdit){
                axios.get(`/role/${this.id}`)
                    .then((response) => {
                        this.roleName = response.data.name;
                    })
            }

        }
    }
</script>

<style scoped>

</style>


